"use client";

import React from "react";
import Link from "next/link";
import { useAuth } from "./layout";

export default function Dashboard() {
  const { user } = useAuth();

  if (!user) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-6">
        <h2 className="text-2xl font-semibold mb-4">Please log in to continue</h2>
        <Link
          href="/login"
          className="bg-black text-white px-6 py-3 rounded hover:bg-gray-800 transition"
        >
          Go to Login
        </Link>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Welcome to JK Mobile Zone Dashboard</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <ModuleCard
          title="Inventory Management"
          description="Manage your mobile phone inventory, add/edit/delete products, and track stock levels."
          href="/inventory"
        />
        <ModuleCard
          title="Client Financial Accounts"
          description="Manage client accounts, track credits, debits, and view transaction history."
          href="/clients"
        />
        <ModuleCard
          title="Daily Financial Summary"
          description="View daily sales, credits, received payments, and profit/loss summary."
          href="/summary"
        />
      </div>
    </div>
  );
}

function ModuleCard({
  title,
  description,
  href,
}: {
  title: string;
  description: string;
  href: string;
}) {
  return (
    <Link
      href={href}
      className="block p-6 border border-gray-300 rounded-lg shadow-sm hover:shadow-md transition"
    >
      <h2 className="text-xl font-semibold mb-2">{title}</h2>
      <p className="text-gray-700">{description}</p>
    </Link>
  );
}
